.pricing .container {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}
.pricing h2 {
    margin: 100px 0 71px;
    font-family: $font-raleway;
    font-size: 36px;
}
.pricing .boxes_wrapper {
    width: inherit;
    display: flex;
    flex-direction: row;
    margin-bottom: 80px;
    align-items: center;
    justify-content: space-evenly;
    gap: 8vw;
}
.pricing .box {
    height: 461px;
    border-radius: 18px;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-between;
    line-height: 1;
    text-align: center;
    max-width: 372px;
    width: 372px;
    min-width: 240px;
}
.box h3 {
    font-size:20px;
    font-family: $font-raleway;
    font-weight: bold;
    margin: 34px 0 0;
}
.box span {
    font-size: 46px;
    font-family: $font-open-sans;
    font-weight: lighter;
}
.box ul {
    margin-top: 20px;
}
.box li {
    font-size: 14px;
    font-family: $font-open-sans;
    line-height: 3;
}
.box button {
    height: 50px;
    width: 150px;
    display: flex;
    align-items: center;
    justify-content: center;
    border: none;
    border-radius: 23px;
    color: white;
    font-size: 13px;
    position: relative;
    top: 25px;
    font-family: $font-open-sans;
    font-weight: bold;
}
.box button:hover {
    color: black;
}
.box small {
    font-size: 12px;
    font-family: $font-raleway;
    font-weight: bold;
}
.box-1 span {
    color: #EB476E;
}
.box-1 {
    border: 1px solid $color-redish;
}
.box-1 button {
    background-color: #EB476E;
}
.box-2 span {
    color: $color-primary;
}
.pricing .box-2 {
    border: 1px solid #07ACE6;
    box-shadow: 5px 10px 27px #07ACE629;
    height: 500px;

}
.box-2 button {
    background-color: $color-primary;
    box-shadow: 5px 10px 14px #07ACE629;
}
.box-3 span {
    color: $color-green;
}
.box-3 {
    border: 1px solid $color-green;
}
.box-3 button {
    background-color: $color-green;
}
.element-checked::before {
    content: "";
    background-image: url("../assets/Checkmark-1.svg");
    display: inline-block;
    height: 11px;
    width: 11px;
    margin-right: 10px;
}
.element-crossed::before {
    content: "";
    background-image: url("../assets/Close.svg");
    display: inline-block;
    height: 11px;
    width: 11px;
    margin-right: 10px;
}
@media (max-width: 1150px) {
.pricing .boxes_wrapper {
    gap: 5vw;
}
}
@media (max-width: 600px) {
    .pricing h2 {
        margin: 50px 0 15px;
    }
    .pricing .boxes_wrapper {

        flex-direction: column;
        gap: 50px;
    }
    .pricing .box {
        height: 250px;
        flex-wrap: wrap;
        align-items: stretch;

        position: relative;
    }
    .pricing button {
        position: absolute;
        top: 225px;
        left: 110px;

    }
    .pricing li {
        line-height: 3;
    }
}
@media (max-width: 450px) {
    .pricing .container .box {
        width: 85vw;
    }
    .pricing img.img-2 {
        padding: 0;
    }
}